.mh-container1{
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100vw;
    

}

.main-container1{
    background-color: #212121;
    width: 40vw;
    height: 100vh;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size: cover;

}
.mh-container1 h1{
    align-self: center;
    font-weight: bold;
}
.login-f{
    width: 60vw;
    height: 100%;
    padding-top: -30px;
}

.login-form{
    padding: 2% 2%;
    padding-left: 20%;
}
.form-container {
    margin-bottom: -10px;
    width: 350px;
    height: 100%;
    background-color: #fff;
    padding: 32px 24px;
    padding-top: -10px;
    font-size: 14px;
    font-family: inherit;
    color: #212121;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
   
}



.form-container .logo-container {
    margin-bottom: 12px;
    text-align: center;
    font-weight: 700;
    font-size: 20px;
}

.form-container .social-buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    flex-direction: column;
    gap: 12px;
    align-items: center;
}

.form-container .social-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-family: inherit;
    color: #fff;
    border: none;
    padding: 12px 16px;
    gap: 8px;
    cursor: pointer;
    border-radius: 6px;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.084), 0px 2px 3px rgba(0, 0, 0, 0.168);
}



.form-container .social-button.google {
    background-color: #1778f2;
}

.form-container .social-button.apple {
    background-color: #212121;
}

.form-container .form {
    display: flex;
    flex-direction: column;
    
}
.form-container{
    padding-top: -20px;
}
.form-container p{
    margin-top: -10px;
    font-size: 16px;
    font-weight: bold;
}

.form-container .form-group {
    display: flex;
    flex-direction: column;
    padding-right: 10%;
    gap: 2px;
    width: 100%;
    padding: 12px 16px;
    border-radius: 6px;
    font-family: inherit;
    margin-top: -10px;
}

.form-container .form-group label {
    display: block;
    margin-bottom: 5px;
}

.form-container .form-group input {
    height: 30px;
    border-radius: 6px;
    border-width: 1px;
    width: 100%;
}
.form-container .form-group input:hover {
    height: 30px;
    border-radius: 6px;
    border-width: 0px;
}


.form-container .form-group input::placeholder {
    opacity: 0.5;
}

.form-container .form-submit-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: inherit;
    color: #fff;
    background-color: #212121;
    border: none;
    width: 100%;
    padding: 12px 16px;
    font-size: inherit;
    gap: 8px;
    margin: 12px 0;
    cursor: pointer;
    border-radius: 6px;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.084), 0px 2px 3px rgba(0, 0, 0, 0.168);
}

.form-container .form-submit-btn:hover {
    background-color: #313131;
}
.signup-link{
    display: flex;
    flex-direction: row;
    
}
.b{
    margin-top: -20px;
}

.bottom-buttons{
    background-color: transparent;
    color: #1778f2;
    border: none;
    outline: none;
    cursor: pointer;
    text-align: center;
    padding: 20px;
}

.bottom-buttons:hover{
    text-decoration: underline;
    background-color: transparent;
    color: #1778f2;
}
.error-message{
    color: red;
}

/* mobile version */
@media screen and (max-width: 768px) {
    .main-container1{
        display: none;
    }
    .mh-container1{
        justify-content: center;
        margin: 0px;
        
    }
    
    .login-f{
        width: 80%;
        height: 100vh;
        margin: 0px;    
    }
  }
.new{
    display: flex;
    
}